:root {
    // font
    --font-poppins: 'Poppins', sans-serif;

    // cursor
    --cursor-default: url('./cursor/Arrow.cur'), default;
    --cursor-pointer: url('./cursor/Hand.cur'), pointer;
    --cursor-text: url('./cursor/IBeam.cur'), text;

    // color
    --background: hsl(0, 0%, 18%);
    --white: hsl(0, 0%, 100%);
    --orange: hsl(33, 93%, 52%);
    --orange-hover: hsl(33, 80%, 46%);

    --card: hsl(0, 0%, 24%);
    --trash: hsla(0, 70%, 51%, 0.6);
    --trash-hover: hsla(0, 70%, 51%, 1);
    --check: hsla(120, 53%, 45%, 0.6);
    --check-hover: hsla(120, 53%, 45%, 1);
}

@import './style/breakpoints';
@import './style/boilerplate';
@import './style/inputComponent';
@import './style/todos';
@import './style/filterDropdown';
